import Vue from 'vue'
import VueRouter from 'vue-router'


const Article = () => import('../components/Article')
const PostList = () => import('../components/PostList')
const UserInfo = () => import('../components/UserInfo')
const SlideBar = () => import('../components/SlideBar')
const HomeSidebar = () => import('../components/HomeSidebar')
const BeginnerGuide = () => import('../components/BeginnerGuide')
const About = () => import('../components/About')


Vue.use(VueRouter)

const routes = [
  {
    name: 'root',
    path: '/',
    components: {
      main: PostList,
      slidebar: HomeSidebar
    }
  },
  {
    name: 'post_content',
    path: '/topic/:id&author=:name',
    components: {
      main: Article,
      slidebar: SlideBar
    }
  },
  {
    name: 'user_info',
    path: '/userinfo/:name',
    components: {
      main: UserInfo,
      slidebar: SlideBar
    }
  },
  {
    name: 'getstart',
    path: '/getstart',
    components: {
      main: BeginnerGuide,
      slidebar: HomeSidebar
    }
  },
  {
    name: 'about',
    path: '/about',
    components: {
      main: About,
      slidebar: HomeSidebar
    }
  }

]

const router = new VueRouter({
  routes,
  // mode: 'history'
})

export default router
